import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

@Component({
  selector: 'app-book',
  templateUrl: './book.page.html',
  styleUrls: ['./book.page.scss'],
})
export class BookPage implements OnInit {
  date: any;
  time: any;

  today: any;
  maxdate: any;
  mindate: any;
  mintime: any;
  maxtime: any;

  minDiffHours = 12;

  bookingdata: any = {
    frequency: '',
    hours: '',
    bedrooms: '',
    bathrooms: '',
  };

  loggedUser: any;

  slideOptions = {
    initialSlide: 0,
    autoplay: true,
    loop: true,
    speed: 2000,
    effect: 'fade',
  };

  showBanner = false;
  banners = [];

  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private iab: InAppBrowser
  ) {
    this.loggedUser = this.localService.getUser();
    if (this.localService.getSettings().hasOwnProperty('booking_min_hours')) {
      this.minDiffHours = this.localService.getSettings().booking_min_hours;
    }
    if (
      this.localService.getSettings().hasOwnProperty('show_banner_users') &&
      this.localService.getSettings().show_banner_users === '1'
    ) {
      this.restService.getData('user/banners').subscribe(
        (data) => {
          this.banners = data;
          if (data.length > 0) {
            this.showBanner = true;
          }
        },
        (err) => {}
      );
    }

    // receive parameters | get cleaner id
    this.activatedRoute.queryParams.subscribe((params: any) => {
      if (params.cleaner_id) {
        this.bookingdata.cleaner_id = params.cleaner_id;
      }
    });

    // auto fill adress & postcode
    this.bookingdata.address = this.loggedUser.address;
    this.bookingdata.postcode = this.loggedUser.postcode;
    this.bookingdata.house_number = this.loggedUser.house_number;

    // calculate minimum datetime
    this.today = new Date();
    this.maxdate = this.maxDate(this.today);
    this.mindate = this.minDate(this.today);

    // override min/max time
    this.mintime = '06:00';
    this.maxtime = '20:00';
  }

  ngOnInit() {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad BookPage');
  }

  maxDate(date) {
    const d = new Date(date);
    let day = '' + d.getDate();
    let month = '' + (d.getMonth() + 1);
    const year = d.getFullYear() + 1;
    if (month.length < 2) {
      month = '0' + month;
    }
    if (day.length < 2) {
      day = '0' + day;
    }
    return [year, month, day].join('-');
  }

  minDate(date) {
    const d = new Date(date);
    let day = '' + d.getDate();
    let month = '' + (d.getMonth() + 1);
    const year = d.getFullYear();
    if (month.length < 2) {
      month = '0' + month;
    }
    if (day.length < 2) {
      day = '0' + day;
    }
    return [year, month, day].join('-');
  }

  next() {
    if (
      !this.bookingdata.house_number ||
      this.bookingdata.house_number === ''
    ) {
      return this.utilityService.openAlerts(
        'Oops',
        'Flat / House Number required.',
        'ok'
      );
    }
    if (!this.bookingdata.address || this.bookingdata.address === '') {
      return this.utilityService.openAlerts(
        'Oops',
        'Valid address required.',
        'ok'
      );
    }
    if (!this.bookingdata.postcode || this.bookingdata.postcode === '') {
      return this.utilityService.openAlerts(
        'Oops',
        'Valid postcode required.',
        'ok'
      );
    }

    if (!this.date || this.date === '') {
      return this.utilityService.openAlerts('Oops', 'Date required.', 'ok');
    }
    if (!this.time || this.time === '') {
      return this.utilityService.openAlerts('Oops', 'Time required.', 'ok');
    }

    if (!this.bookingdata.frequency || this.bookingdata.frequency === '') {
      return this.utilityService.openAlerts('Oops', 'Select Frequency.', 'ok');
    }
    if (!this.bookingdata.hours || this.bookingdata.hours === '') {
      return this.utilityService.openAlerts('Oops', 'Select Hours.', 'ok');
    }
    if (!this.bookingdata.bedrooms || this.bookingdata.bedrooms === '') {
      return this.utilityService.openAlerts('Oops', 'Select Bedrooms.', 'ok');
    }
    if (!this.bookingdata.bathrooms || this.bookingdata.bathrooms === '') {
      return this.utilityService.openAlerts('Oops', 'Select Bathrooms.', 'ok');
    }

    // format date time as per ionic5
    this.bookingdata.date = this.date.split('T')[0];
    const time = this.time.split('T')[1].split('.')[0].split(':');
    this.bookingdata.time = time[0] + ':' + time[1];

    // validate time after amount of hours

    const currentTimeString = new Date().toLocaleString('en-US', {});

    console.log(currentTimeString);
    const currentTime = new Date(currentTimeString).getTime();

    const selectedTimeString = new Date(
      this.bookingdata.date + 'T' + this.bookingdata.time
    ).toLocaleString('en-US', {});
    const selectedTime = new Date(selectedTimeString).getTime();

    const difference = selectedTime - currentTime;

    console.log(currentTime, selectedTime, difference);

    if (selectedTime - currentTime < this.minDiffHours * 3600 * 1000) {
      return this.utilityService.openAlerts(
        'Oops',
        'The earliest you can book is ' +
          this.minDiffHours +
          ' hours before the job starts.',
        'ok'
      );
    }
    this.router.navigate(['/customise'], {
      queryParams: {
        data: JSON.stringify(this.bookingdata),
      },
    });
  }

  openLink(link) {
    if (link && link !== '' && link !== '#') {
      this.iab.create(link, '_blank', {
        location: 'no',
        hardwareback: 'no',
        hidenavigationbuttons: 'yes',
        hideurlbar: 'yes',
        fullscreen: 'yes',
        zoom: 'no',
        clearcache: 'yes',
        toolbar: 'yes',
      });
    }
  }
}
